
*{
  margin:0;
  padding:0;
}
html {
  overflow-x: hidden;
  overflow-y: auto;
}
ul{
  list-style:none;
}
a{
 text-decoration: none;
}
p{
  margin-bottom:0;
}



.tabbar-box{
  width: 100%;
  height:3.5rem;
  position:fixed;
  bottom:0;
  left:0;
  background-color: #ededed;
  z-index:999;
}

.tabbar-item{
  width: 25%;
  height:4rem;
  text-align: center;
  float: left;
}
.tabbar-item img{
  width: 50%;
  height:2.5rem;
  -webkit-transform:scale(.7);
          transform:scale(.7);
}
.tabbar-item p{
  font-size:0.65rem;
  vertical-align:top;
  color: #919191;
}
.tabbar-item .active{
  color: #1448ae;
}

.topbar{
  text-align:center;
  font-size:0.75rem;
  color: #fff;
}


.lunbo{
   max-width:18.75rem;
   margin:0 auto;
   min-width:8rem;
}
.bb{
   width:100%;
   height:2.2rem;
   position:fixed;
   top:0;
   z-index:100;
   background-color:#000;
   opacity:.4;
   left:0;
}
.bb .bg{
   width: 90%;
   height:1.325rem;
   margin:0.375rem auto;
   position:relative;
   line-height:1.325rem;
}
.bg span{
   position: absolute;
   top:0.275rem;
   left:0.35rem;
   width: 0.85rem;
   height:0.85rem;
   background:url(data:image/png;base64,) no-repeat;
   background-size:cover;
}
.bb input{
   width:100%;
   height:100%;
   outline-style:none;
   border-radius:20px;
   border:1px solid #bfc6c3;
   background-color:transparent;
}
.bb input::-webkit-input-placeholder{
   font-size:0.7rem;
   color:#bfc6c3;
   text-indent:1.5rem;
}
